<template>
  <table className='table table-striped table-hover'>
        <thead>
            <tr>
                <th>Fecha</th>
                <th>Descripcion</th>
                <th>Monto</th>
                <th>Editar</th>
                <th>Eliminar</th>
            </tr>     
        </thead>
        <tbody>
 
            <tr  v-for="ing in getIngresosAll" :key="ing._id">
                <td>{{ing.fecha.slice(0,10)}}</td>
                <td>{{ing.descripcion}}</td>
                <td>{{ing.monto}}</td>

                <td>
                    <button
                        className='btn btn-secondary'
                        :disabled="getIsLoading"
                        @click="setIngreso(ing)"
                    >
                        Editar
                    </button>
                </td>
                <td>
                    <button
                        className='btn btn-danger'
                        :disabled="getIsLoading"
                        @click="deleteIngreso(ing._id)"
                    >
                        Eliminar
                    </button>
                </td>

            </tr>

        </tbody>
    
    </table>
    
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';

export default {
   
    computed:{
        ...mapGetters('ferneyIngreso',['getIngresosAll','getIsLoading']),
    },
    methods:{
        ...mapActions('ferneyIngreso',['getIngresos','deleteIngreso']),
        ...mapMutations('ferneyIngreso',['setIngreso'])
    },
    created(){
        this.getIngresos()
    }
}
</script>

